      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.player-button img {
  height: 1.25rem;
  width: 1.25rem;
}
.fullscreen:hover,
.toggle:hover {
